import React, { useState, useEffect } from "react";
import { useParams, Link } from "react-router-dom";

import {
  FaCalendar,
  FaMapMarkerAlt,
  FaTag,
  FaClock,
  FaUser,
  FaUsers,
} from "react-icons/fa";
import Header from "../components/Header/Header";
import Footer from "../components/Footer/Footer";
import styles from "../styles/EventDetail.module.css";

const EventDetail = () => {
  const { id } = useParams();
  const [event, setEvent] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [pageTitle, setPageTitle] = useState("Loading...");

  useEffect(() => {
    const fetchEventDetails = async () => {
      try {
        setLoading(true);
        const response = await fetch(`http://localhost:4000/api/events/${id}`);
        if (!response.ok) {
          throw new Error("Event not found");
        }
        const data = await response.json();
        setEvent(data);
        setPageTitle(data.title);
      } catch (err) {
        setError(err.message);
        setPageTitle("Event Not Found");
      } finally {
        setLoading(false);
      }
    };

    fetchEventDetails();
  }, [id]);

  const renderContent = () => {
    if (loading) {
      return (
        <div className={styles.loadingContainer}>
          <div className={styles.loader}></div>
          <p>Loading event details...</p>
        </div>
      );
    }

    if (error) {
      return (
        <div className={styles.errorContainer}>
          <h2>Error</h2>
          <p>{error}</p>
          <Link to="/events" className={styles.backButton}>
            Back to Events
          </Link>
        </div>
      );
    }

    if (!event) {
      return (
        <div className={styles.errorContainer}>
          <h2>Event Not Found</h2>
          <p>The event you're looking for doesn't exist.</p>
          <Link to="/events" className={styles.backButton}>
            Back to Events
          </Link>
        </div>
      );
    }

    const eventDate = new Date(event.event_date);
    const formattedDate = eventDate.toLocaleDateString("en-US", {
      weekday: "long",
      year: "numeric",
      month: "long",
      day: "numeric",
    });
    const formattedTime = eventDate.toLocaleTimeString("en-US", {
      hour: "2-digit",
      minute: "2-digit",
    });

    return (
      <>
        <div className={styles.navigation}>
          <Link to="/events" className={styles.backLink}>
            ← Back to Events
          </Link>
        </div>

        <div className={styles.heroSection}>
          <div className={styles.eventImage}>
            {event.photo_url ? (
              <img src={event.photo_url} alt={event.title} />
            ) : (
              <div className={styles.placeholderImage}>
                <FaCalendar />
              </div>
            )}
          </div>
        </div>

        <div className={styles.content}>
          <div className={styles.mainContent}>
            <div className={styles.description}>
              <h2>About This Event</h2>
              <p>{event.description}</p>
            </div>

            {event.schedule && (
              <div className={styles.schedule}>
                <h2>Event Schedule</h2>
                <div className={styles.scheduleList}>
                  {event.schedule.map((item, index) => (
                    <div key={index} className={styles.scheduleItem}>
                      <div className={styles.scheduleTime}>{item.time}</div>
                      <div className={styles.scheduleActivity}>
                        {item.activity}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>

          <aside className={styles.sidebar}>
            <div className={styles.infoCard}>
              <div className={styles.infoItem}>
                <FaCalendar />
                <div>
                  <h3>Date</h3>
                  <p>{formattedDate}</p>
                </div>
              </div>

              <div className={styles.infoItem}>
                <FaClock />
                <div>
                  <h3>Time</h3>
                  <p>{formattedTime}</p>
                </div>
              </div>

              {event.location && (
                <div className={styles.infoItem}>
                  <FaMapMarkerAlt />
                  <div>
                    <h3>Location</h3>
                    <p>{event.location}</p>
                  </div>
                </div>
              )}

              {event.organizer && (
                <div className={styles.infoItem}>
                  <FaUser />
                  <div>
                    <h3>Organizer</h3>
                    <p>{event.organizer}</p>
                  </div>
                </div>
              )}

              {event.capacity && (
                <div className={styles.infoItem}>
                  <FaUsers />
                  <div>
                    <h3>Capacity</h3>
                    <p>{event.capacity} attendees</p>
                  </div>
                </div>
              )}
            </div>

            {event.registration_required && (
              <button className={styles.registerButton}>Register Now</button>
            )}
          </aside>
        </div>
      </>
    );
  };

  return (
    <div className={styles.page}>
      <Header title="Event" subtitle={pageTitle} />
      <main className={styles.container}>{renderContent()}</main>
      <Footer />
    </div>
  );
};

export default EventDetail;
