import React from "react";
import Header from "../components/Header/Header";
import Footer from "../components/Footer/Footer";
import styles from "../styles/Policy.module.css";

const PrivacyPolicy = () => {
  return (
    <div className={styles.page}>
      <Header title="Privacy Policy" subtitle="Your Privacy Matters" />
      <main className={styles.container}>
        <h1>Privacy Policy</h1>
        <p>
          Welcome to DSAL. Your privacy is important to us. This Privacy Policy
          explains how we collect, use, and protect your information.
        </p>
        
        <h2>1. Information We Collect</h2>
        <p>We collect personal data that you provide when using our services, such as name, email, and any relevant details.</p>

        <h2>2. How We Use Your Data</h2>
        <p>Your data is used to improve our services, personalize experiences, and communicate updates.</p>

        <h2>3. Data Protection</h2>
        <p>We implement security measures to protect your personal data from unauthorized access.</p>

        <h2>4. Your Rights</h2>
        <p>You have the right to access, update, or request deletion of your data.</p>

        <h2>5. Contact Us</h2>
        <p>If you have any questions about this policy, please contact us at nzaki@uaeu.ac.ae</p>
      </main>
      <Footer />
    </div>
  );
};

export default PrivacyPolicy;
