import React, { useState, useEffect, useLayoutEffect } from "react";
import { Link } from "react-router-dom";
import Header from "../components/Header/Header";
import Footer from "../components/Footer/Footer";
import styles from "../styles/Projects.module.css";

const ProjectsPage = () => {
  const [projects, setProjects] = useState([]);
  const [types, setTypes] = useState([]);
  const [activeFilter, setActiveFilter] = useState("All");
  const [loading, setLoading] = useState(true);
  const [scrollPosition, setScrollPosition] = useState(0);

  // Fetch projects when filter changes
  useEffect(() => {
    let isMounted = true;
    
    const fetchProjects = async () => {
      console.log("Fetching projects..."); // Debugging log

      try {
        setLoading(true); // Make sure loading is set to true before fetching

        const queryParams = new URLSearchParams();
        if (activeFilter !== "All") {
          queryParams.append("type", activeFilter);
        }
        const response = await fetch(
          `http://localhost:4000/api/projects?${queryParams}`
        );
        if (!response.ok) throw new Error("Failed to fetch projects");
        const data = await response.json();
        
        if (isMounted) {
          console.log("Projects fetched successfully:", data); // Debugging log

          setLoading(false);
          setProjects(data);
        }
      } catch (error) {
        console.error("Error fetching projects:", error);
        if (isMounted) {
          setLoading(false);
        }
      }
    };

    fetchProjects();
    return () => {
      isMounted = false;
    };
  }, [activeFilter]);

  // Fetch project types
  useEffect(() => {
    const fetchTypes = async () => {
      try {
        const response = await fetch("http://localhost:4000/api/project-types");
        if (!response.ok) throw new Error("Failed to fetch types");
        const data = await response.json();
        setTypes(data);
      } catch (error) {
        console.error("Error:", error);
      }
    };
    fetchTypes();
  }, []);

  // Save scroll position before filter change
  useLayoutEffect(() => {
    // Restore scroll position after render
    if (!loading) {
      window.scrollTo(0, scrollPosition);
    }
  }, [loading, projects, scrollPosition]);

  const handleFilterClick = (newFilter, e) => {
    e.preventDefault();
    // Save current scroll position
    setScrollPosition(window.scrollY);
    // Update filter without using loading state
    if (newFilter !== activeFilter) {
      setActiveFilter(newFilter);
    }
  };

  if (loading) {
    return (
      <div className={styles["main-content"]}>
        <Header title="Projects" subtitle="Data Science and Analytics Lab" />
        
        {/* Show a loading screen in place of the content */}
        <div style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          height: "calc(100vh - 80px)", // Adjust height to fit below header
          width: "100vw",
          background: "black",
          color: "white",
          fontSize: "24px",
          fontWeight: "bold",
          textTransform: "uppercase",
        }}>
          Loading...
        </div>
  
        <Footer />
      </div>
    );
  }
  
  
  
  return (
    <div className={styles["main-content"]}>
      <Header title="Projects" subtitle="Data Science and Analytics Lab" />
      
      <div className="title-container">
        <h3 className="section-subtitle">Projects</h3>
        <h1 className="section-title">Innovations in Data Analytics</h1>
      </div>

      <div 
        className={styles.filterButtons}
        style={{ position: 'sticky', top: 0, zIndex: 10, background: 'white' }}
      >
        <button
          className={`${styles.filterButton} ${
            activeFilter === "All" ? styles.active : ""
          }`}
          onClick={(e) => handleFilterClick("All", e)}
        >
          Show All
        </button>
        {types.map((type) => (
          <button
            key={type.id}
            className={`${styles.filterButton} ${
              activeFilter === type.name ? styles.active : ""
            }`}
            onClick={(e) => handleFilterClick(type.name, e)}
          >
            {type.name}
          </button>
        ))}
      </div>

      <div className={styles.projectsContainer}>
        {projects.map((project) => (
          <Link
            to={`/projects/${project.id}#project-details`}
            key={project.id}
            className={styles.projectItem}
          >
            <img
              src={project.image_path || "/images.png"}
              alt={project.title}
              className={styles.projectImage}
            />
            <div className={styles.projectText}>
              <h3 className={styles.projectTitle}>{project.title}</h3>
              <p className={styles.projectDescription}>{project.description}</p>
              <span className={styles.readMore}>Read More →</span>
            </div>
          </Link>
        ))}
      </div>

      {projects.length === 0 && !loading && (
        <div className={styles.noProjects}>
          <h3>No projects found</h3>
          <p>Try adjusting your filters or check back later for new projects.</p>
        </div>
      )}
      <Footer />
    </div>
  );
};

export default ProjectsPage;