import React, { useState, useEffect,useRef } from "react";
import { Search } from "lucide-react";
import Header from "../components/Header/Header";
import Footer from "../components/Footer/Footer";
import styles from "../styles/Publications.module.css";
import { Link } from "react-router-dom";


const PublicationsPage = () => {
  const [publications, setPublications] = useState([]);
  const [filteredPublications, setFilteredPublications] = useState([]);
  const [filterOptions, setFilterOptions] = useState({
    types: [],
    keywords: [],
    journals: [],
    conferences: [],
    years: [],
  });
  const [filters, setFilters] = useState({
    type: "",
    keyword: "",
    year: "",
    searchTerm: "",
  });
  const [loading, setLoading] = useState(true);
  const [sortBy, setSortBy] = useState("newest");
  
  // Add ref for content layout
  const contentLayoutRef = useRef(null);

  // Fetch initial data and filter options
  useEffect(() => {
    const fetchData = async () => {
      try {
        const pubResponse = await fetch("http://localhost:4000/api/publications");
        const pubData = await pubResponse.json();
        setPublications(pubData);
        setFilteredPublications(pubData);

        const filterResponse = await fetch("http://localhost:4000/api/publications/filters");
        const filterData = await filterResponse.json();
        setFilterOptions(filterData);

        setLoading(false);
      } catch (error) {
        console.error("Error fetching data:", error);
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  useEffect(() => {
    const fetchFilteredPublications = async () => {
      try {
        const queryParams = new URLSearchParams();
        if (filters.type) queryParams.append("type", filters.type);
        if (filters.keyword) queryParams.append("keyword", filters.keyword);
        if (filters.year) queryParams.append("year", filters.year);

        const response = await fetch(`http://localhost:4000/api/publications?${queryParams}`);
        const data = await response.json();

        let filtered = data;
        if (filters.searchTerm) {
          const searchLower = filters.searchTerm.toLowerCase();
          filtered = filtered.filter(
            (pub) =>
              pub.title.toLowerCase().includes(searchLower) ||
              pub.authors?.toLowerCase().includes(searchLower)
          );
        }

        setFilteredPublications(filtered);
        
        // Scroll to content layout when filters change
        if (contentLayoutRef.current) {
          contentLayoutRef.current.scrollIntoView({ behavior: 'smooth' });
        }
      } catch (error) {
        console.error("Error fetching filtered publications:", error);
      }
    };

    fetchFilteredPublications();
  }, [filters]);

  const handleSearch = (event) => {
    setFilters((prev) => ({
      ...prev,
      searchTerm: event.target.value,
    }));
  };

  const handleFilterChange = (filterType, value) => {
    setFilters((prev) => ({
      ...prev,
      [filterType]: prev[filterType] === value ? "" : value,
    }));
  };

  const handleReset = () => {
    setFilters({
      type: "",
      keyword: "",
      year: "",
      searchTerm: "",
    });
  };

  const formatAuthors = (authors) => {
    if (!authors) return "";
    const authorArray = authors.split(",");
    return authorArray.length <= 7
      ? authors
      : authorArray.slice(0, 7).join(", ") + "...";
  };

  return (
    <div className={styles["main-content"]}>
      <div className={styles["top-section"]}>
        <Header
          title="Publications"
          subtitle="Data Science and Analytics Lab"
        />
        <div className={styles["fade-line"]}></div>
      </div>

      <div className={styles["white-section"]}></div>
      <div className="title-container">
        <h3 className="section-subtitle">Publications</h3>
        <h1 className="section-title">All Researchs</h1>
      </div>
      
      <div className={styles.container}>
        <div className={styles.header}>
          <div className={styles["sort-container"]}>
            <span>Sort by:</span>
            <select
              className={styles["sort-select"]}
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value)}
            >
              <option value="newest">Newest</option>
              <option value="oldest">Oldest</option>
              <option value="cited">Most cited</option>
            </select>
          </div>
        </div>

        <div className={styles["content-layout"]} ref={contentLayoutRef}>
          {/* Left Sidebar */}
          <div className={styles.sidebar}>
            <div className={styles["filter-card"]}>
              <div className={styles["search-container"]}>
                <input
                  type="text"
                  placeholder="Search research"
                  className={styles["search-input"]}
                  value={filters.searchTerm}
                  onChange={handleSearch}
                />
                <Search className={styles["search-icon"]} size={20} />
              </div>

              <button
                onClick={handleReset}
                className={styles["reset-button"]}
                disabled={
                  !filters.searchTerm &&
                  !filters.type &&
                  !filters.keyword &&
                  !filters.year
                }
              >
                Reset filters
              </button>

              <div className={styles["filter-section"]}>
                <h3>Content type</h3>
                <div className={styles["checkbox-group"]}>
                  {filterOptions.types?.map((type, index) => (
                    <label key={index} className={styles["checkbox-label"]}>
                      <input
                        type="checkbox"
                        checked={filters.type === type.type}
                        onChange={() => handleFilterChange("type", type.type)}
                      />
                      {type.type}
                    </label>
                  ))}
                </div>
              </div>

              <div className={styles["filter-section"]}>
                <h3>Keywords</h3>
                <div className={styles["keyword-list"]}>
                  {filterOptions.keywords?.map((keyword, index) => (
                    <div
                      key={index}
                      className={`${styles["keyword-item"]} ${
                        filters.keyword === keyword.keyword
                          ? styles.selected
                          : ""
                      }`}
                      onClick={() =>
                        handleFilterChange("keyword", keyword.keyword)
                      }
                    >
                      <div
                        className={styles["keyword-dot"]}
                        style={{ backgroundColor: keyword.color }}
                      />
                      <span>{keyword.keyword}</span>
                    </div>
                  ))}
                </div>
              </div>

              <div className={styles["filter-section"]}>
                <h3>Publication Year</h3>
                <div className={styles["checkbox-group"]}>
                  {filterOptions.years?.map((yearObj, index) => (
                    <label key={index} className={styles["checkbox-label"]}>
                      <input
                        type="checkbox"
                        checked={
                          filters.year === yearObj.publication_year.toString()
                        }
                        onChange={() =>
                          handleFilterChange(
                            "year",
                            yearObj.publication_year.toString()
                          )
                        }
                      />
                      {yearObj.publication_year}
                    </label>
                  ))}
                </div>
              </div>
            </div>
          </div>

          {/* Main Content */}
          <div className={styles["main-content"]}>
            <div className={styles["publication-list"]}>
              {loading ? (
                <div className={styles.loading}>Loading...</div>
              ) : filteredPublications.length === 0 ? (
                <div className={styles["no-results"]}>
                  No publications found matching your criteria
                </div>
              ) : (
                filteredPublications.map((pub, index) => (
                  <div key={index} className={styles["publication-card"]}>
                    <div className={styles["publication-meta"]}>
                      {pub.type} | {pub.journal_title} {pub.publication_year}
                    </div>
                    <h2 className={styles["publication-title"]}>
                      <Link to={`/publications/${encodeURIComponent(pub.doi)}`}>
                        {pub.title}
                      </Link>
                    </h2>
                    <p className={styles["publication-authors"]}>
                      {formatAuthors(pub.authors)}
                    </p>
                  </div>
                ))
              )}
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default PublicationsPage;
