import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import Header from "../components/Header/Header";
import Footer from "../components/Footer/Footer";
import styles from "../styles/Events.module.css";


const EventCard = ({ event }) => {
  const date = new Date(event.event_date);
  const month = date.toLocaleString("default", { month: "short" });
  const day = date.getDate();
  const year = date.getFullYear();

  return (
    <div className={styles.card}>
      <div className={styles.dateContainer}>
        <div className={styles.dateBox}>
          <span className={styles.month}>{month}</span>
          <span className={styles.day}>{day}</span>
          <span className={styles.year}>{year}</span>
        </div>
      </div>
      <div className={styles.content}>
        <h3 className={styles.title}>{event.title}</h3>
        <div className={styles.info}>
          <span className={styles.type}>
            <i className="fas fa-tag"></i> {event.type}
          </span>
          {event.location && (
            <span className={styles.location}>
              <i className="fas fa-map-marker-alt"></i> {event.location}
            </span>
          )}
        </div>
        <p className={styles.description}>
          {event.description?.substring(0, 150)}...
        </p>
        <Link to={`/event/${event.event_id}`} className={styles.readMore}>
          Read More <i className="fas fa-arrow-right"></i>
        </Link>
      </div>
    </div>
  );
};

const Events = () => {
  const [events, setEvents] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [filter, setFilter] = useState("all");

  useEffect(() => {
    const fetchEvents = async () => {
      try {
        const response = await fetch("http://localhost:4000/api/events");
        if (!response.ok) {
          throw new Error("Failed to fetch events");
        }
        const data = await response.json();
        setEvents(data);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchEvents();
  }, []);

  const filteredEvents =
    filter === "all"
      ? events
      : events.filter((event) => event.type.toLowerCase() === filter);

  const uniqueTypes = [
    "all",
    ...new Set(events.map((event) => event.type.toLowerCase())),
  ];

  return (
    <div className={styles.page}>
      <Header title={"Events"} subtitle={"Data Science and Analytics Lab"} />
      <main className={styles.container}>
        <section className={styles.header}>
          <div class="title-container">
            <h3 class="section-subtitle">EVENTS</h3>
            <h1 class="section-title">DSAL Events</h1>
          </div>
          <div className={styles.filterContainer}>
            {uniqueTypes.map((type) => (
              <button
                key={type}
                className={`${styles.filterBtn} ${
                  filter === type ? styles.active : ""
                }`}
                onClick={() => setFilter(type)}
              >
                {type.charAt(0).toUpperCase() + type.slice(1)}
              </button>
            ))}
          </div>
        </section>

        {loading ? (
          <div className={styles.loadingState}>
            <div className={styles.loader}></div>
            <p>Loading events...</p>
          </div>
        ) : error ? (
          <div className={styles.errorState}>
            <p>Error: {error}</p>
            <button
              onClick={() => window.location.reload()}
              className={styles.retryBtn}
            >
              Try Again
            </button>
          </div>
        ) : (
          <div className={styles.grid}>
            {filteredEvents.length > 0 ? (
              filteredEvents.map((event) => (
                <EventCard key={event.event_id} event={event} />
              ))
            ) : (
              <div className={styles.noEvents}>
                <p>No events found for this category.</p>
              </div>
            )}
          </div>
        )}
      </main>
      <Footer />
    </div>
  );
};

export default Events;
