import React, { useState, useEffect } from "react";
import Header from "../components/Header/Header";
import Footer from "../components/Footer/Footer";
import styles from "../styles/TeamMembers.module.css";


const MembersPage = () => {
  const [members, setMembers] = useState({
    directors: [],
    investigator: [],
    researchAssistants: [],
    PhDStudents: [],
    masterDegreeStudents: [],
    formerStudents: [],
    undergraduateStudents: [],
    internshipStudents: [],
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const handleMemberClick = (member) => {
    if (
      ["Director", "Investigator", "Research Assistant"].includes(
        member.member_type
      )
    ) {
      window.location.href = `/member/${member.member_id}`;
    }
  };

  useEffect(() => {
    const fetchMembers = async () => {
      try {
        const response = await fetch("http://localhost:4000/api/members");
        if (!response.ok) {
          throw new Error("Failed to fetch members");
        }
        const data = await response.json();
        console.log("Fetched data:", data);

        const currentMembers = data.filter(
          (member) => member.status === "current"
        );
        const formerMembers = data.filter(
          (member) => member.status === "former"
        );

        const categorizedMembers = {
          directors: currentMembers.filter(
            (member) => member.member_type === "Director"
          ),
          investigator: currentMembers.filter(
            (member) => member.member_type === "Investigator"
          ),
          researchAssistants: currentMembers.filter(
            (member) => member.member_type === "Research Assistant"
          ),
          PhDStudents: currentMembers.filter(
            (member) => member.member_type === "PhD Student"
          ),
          masterDegreeStudents: currentMembers.filter(
            (member) => member.member_type === "Master Degree Student"
          ),
          undergraduateStudents: currentMembers.filter(
            (member) => member.member_type === "Undergraduate Student"
          ),
          internshipStudents: currentMembers.filter(
            (member) => member.member_type === "Internship Student"
          ),
          formerStudents: formerMembers,
        };

        setMembers(categorizedMembers);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchMembers();
  }, []);

  if (loading) {
    return (
      <div className={styles["main-content"]}>
        <Header title="Our Team" subtitle="Data Science and Analytics Lab" />
        
        {/* Show a loading screen in place of the content */}
        <div style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          height: "calc(100vh - 80px)", // Adjust height to fit below header
          width: "100vw",
          background: "black",
          color: "white",
          fontSize: "24px",
          fontWeight: "bold",
          textTransform: "uppercase",
        }}>
          Loading...
        </div>
  
        <Footer />
      </div>
    );
  }
  
  
  
    if (error) return <div className={styles.error}>Error: {error}</div>;

  return (
    <div className={styles["main-content"]}>
      <Header title="Our Team" subtitle="Data Science and Analytics Lab" />
      <div className={styles["main-mid-content"]}>
        <div className="white-section"></div>
        <div className="title-containter">
          <h3 className="section-subtitle">OUR TEAM</h3>
          <h1 className="section-title">All Members</h1>
        </div>

        {/* Directors Section */}
        {members.directors.length > 0 && (
          <div className={styles["team-category"]}>
            <h2>Directors</h2>
            <div className={styles["team-grid"]}>
              {members.directors.map((member) => (
                <div
                  key={member.member_id}
                  className={styles["team-member"]}
                  onClick={() => handleMemberClick(member)}
                  style={{
                    cursor: ["Director", "Investigator", "Research Assistant"].includes(
                      member.member_type
                    )
                      ? "pointer"
                      : "default",
                  }}
                >
                  <img
                    src={member.profile_image || "person1.jpg"}
                    alt={`${member.first_name} ${member.last_name}`}
                  />
                  <h3>{`${member.first_name} ${member.last_name}`}</h3>
                  <p>{member.title}</p>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Investigators Section */}
        {members.investigator.length > 0 && (
          <div className={styles["team-category"]}>
            <h2>Investigators</h2>
            <div className={styles["team-grid"]}>
              {members.investigator.map((member) => (
                <div
                  key={member.member_id}
                  className={styles["team-member"]}
                  onClick={() => handleMemberClick(member)}
                  style={{
                    cursor: ["Director", "Investigator", "Research Assistant"].includes(
                      member.member_type
                    )
                      ? "pointer"
                      : "default",
                  }}
                >
                  <img
                    src={member.profile_image || "person1.jpg"}
                    alt={`${member.first_name} ${member.last_name}`}
                  />
                  <h3>{`${member.first_name} ${member.last_name}`}</h3>
                  <p>{member.title}</p>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Research Assistants Section */}
        {members.researchAssistants.length > 0 && (
          <div className={styles["team-category"]}>
            <h2>Research Assistants</h2>
            <div className={styles["team-grid"]}>
              {members.researchAssistants.map((member) => (
                <div
                  key={member.member_id}
                  className={styles["team-member"]}
                  onClick={() => handleMemberClick(member)}
                  style={{
                    cursor: ["Director", "Investigator", "Research Assistant"].includes(
                      member.member_type
                    )
                      ? "pointer"
                      : "default",
                  }}
                >
                  <img
                    src={member.profile_image || "person1.jpg"}
                    alt={`${member.first_name} ${member.last_name}`}
                  />
                  <h3>{`${member.first_name} ${member.last_name}`}</h3>
                  <p>{member.title}</p>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Students Section */}
        {(members.PhDStudents.length > 0 ||
          members.masterDegreeStudents.length > 0 ||
          members.undergraduateStudents.length > 0 ||
          members.internshipStudents.length > 0) && (
          <div className={styles["team-category"]}>
            <h2>Students</h2>

            {/* PhD Students Subsection */}
            {members.PhDStudents.length > 0 && (
              <div>
                <h3 className={styles["sub-category"]}>PhD Students</h3>
                <div className={styles["team-grid"]}>
                  {members.PhDStudents.map((member) => (
                    <div
                      key={member.member_id}
                      className={styles["team-member"]}
                    >
                      <img
                        src={member.profile_image || "person1.jpg"}
                        alt={`${member.first_name} ${member.last_name}`}
                      />
                      <h3>{`${member.first_name} ${member.last_name}`}</h3>
                      <p>{member.title}</p>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/* Master's Degree Students Subsection */}
            {members.masterDegreeStudents.length > 0 && (
              <div>
                <h3 className={styles["sub-category"]}>Master's Degree Students</h3>
                <div className={styles["team-grid"]}>
                  {members.masterDegreeStudents.map((member) => (
                    <div
                      key={member.member_id}
                      className={styles["team-member"]}
                    >
                      <img
                        src={member.profile_image || "person1.jpg"}
                        alt={`${member.first_name} ${member.last_name}`}
                      />
                      <h3>{`${member.first_name} ${member.last_name}`}</h3>
                      <p>{member.title}</p>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/* Undergraduate Students Subsection */}
            {members.undergraduateStudents.length > 0 && (
              <div>
                <h3 className={styles["sub-category"]}>Undergraduate Students</h3>
                <div className={styles["team-grid"]}>
                  {members.undergraduateStudents.map((member) => (
                    <div
                      key={member.member_id}
                      className={styles["team-member"]}
                    >
                      <img
                        src={member.profile_image || "person1.jpg"}
                        alt={`${member.first_name} ${member.last_name}`}
                      />
                      <h3>{`${member.first_name} ${member.last_name}`}</h3>
                      <p>{member.title}</p>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/* Internship Students Subsection */}
            {members.internshipStudents.length > 0 && (
              <div>
                <h3 className={styles["sub-category"]}>Internship Students</h3>
                <div className={styles["team-grid"]}>
                  {members.internshipStudents.map((member) => (
                    <div
                      key={member.member_id}
                      className={styles["team-member"]}
                    >
                      <img
                        src={member.profile_image || "/api/placeholder/200/200"}
                        alt={`${member.first_name} ${member.last_name}`}
                        onError={(e) => {
                          if (!e.target.hasAttribute("data-error-handled")) {
                            e.target.setAttribute("data-error-handled", "true");
                            e.target.src = "/api/placeholder/200/200";
                          }
                        }}
                      />
                      <h3>{`${member.first_name} ${member.last_name}`}</h3>
                      <p>{member.title}</p>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        )}

        {/* Former Members Section */}
        {members.formerStudents.length > 0 && (
          <div className={styles["team-category"]}>
            <h2>Former Members</h2>
            <div className={styles["team-grid"]}>
              {members.formerStudents.map((member) => (
                <div key={member.member_id} className={styles["team-member"]}>
                  <img
                    src={member.profile_image || "person1.jpg"}
                    alt={`${member.first_name} ${member.last_name}`}
                  />
                  <h3>{`${member.first_name} ${member.last_name}`}</h3>
                  <p>{member.title}</p>
                  <p>{member.member_type}</p>
                </div>
              ))}
            </div>
          </div>
        )}
      </div>

      <Footer />
    </div>
  );
};

export default MembersPage;