import React, { useState, useEffect, useRef } from "react";
import { useParams, useLocation } from "react-router-dom";
import styles from "../styles/ProjectItem.module.css";
import Header from "../components/Header/Header";
import Footer from "../components/Footer/Footer";

const ProjectItem = () => {
  const [project, setProject] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const { id } = useParams();
  const location = useLocation();
  const projectDetailsRef = useRef(null);

  useEffect(() => {
    const fetchProject = async () => {
      try {
        console.log("Fetching project with ID:", id);
        const response = await fetch(
          `http://localhost:4000/api/projects/${id}`
        );
        
        console.log("Response status:", response.status);
        
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        
        const data = await response.json();
        console.log("Received project data:", data);
        
        if (!data) {
          throw new Error("No data received");
        }
        
        setProject(data);
      } catch (error) {
        console.error("Error fetching project:", error);
        setError(error.message);
      } finally {
        setLoading(false);
      }
    };

    if (id) {
      fetchProject();
    }
  }, [id]);

  useEffect(() => {
    if (location.hash === '#project-details' && !loading && project && projectDetailsRef.current) {
      setTimeout(() => {
        projectDetailsRef.current.scrollIntoView({ behavior: 'smooth' });
      }, 100);
    }
  }, [location.hash, loading, project]);

  if (loading) {
    return (
      <div className={styles["main-content"]}>
        <Header title="" subtitle="" />
        
        {/* Show a loading screen in place of the content */}
        <div style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          height: "calc(100vh - 80px)", // Adjust height to fit below header
          width: "100vw",
          background: "black",
          color: "white",
          fontSize: "24px",
          fontWeight: "bold",
          textTransform: "uppercase",
        }}>
          Loading...
        </div>
  
        <Footer />
      </div>
    );
  }
  

  if (error) {
    return (
      <div className={styles.errorContainer}>
        <h1>Error loading project</h1>
        <p>{error}</p>
      </div>
    );
  }

  if (!project) {
    return (
      <div className={styles.projectDetail}>
        <h1>Project not found</h1>
      </div>
    );
  }

  return (
    <div className={styles["main-content"]}>
      <Header title="Projects" subtitle="Data Science and Analytics Lab" />
    
      <div className="title-container">
        <h3 className="section-subtitle">Projects</h3>
        <h1 className="section-title">Innovations in Data Analytics</h1>
      </div>

      <div className={styles.projectDetail}>
        <div 
          ref={projectDetailsRef}
          className={`${styles.researchTeam} ${styles.scrollTarget}`}
        >
          <div className={styles.projectHeader}>
    <img
        src={project.image_path || "/imageswide.png"}
        alt={project.title}
        className={styles.projectMainImage}
    />
    <h1 className={styles.projectTitle}>{project.title}</h1>
    <p className={styles.projectDescription}>{project.description}</p>

    {/* Render Video If Available */}
    {project.video_link && (
        <div className={styles.videoContainer}>
            <h3>Project Video</h3>
            <iframe
                width="560"
                height="315"
                src={project.video_link.replace("watch?v=", "embed/")}
                frameBorder="0"
                allowFullScreen
                className={styles.projectVideo}
            ></iframe>
        </div>
    )}
</div>

        </div>

        {project.researchers && project.researchers.length > 0 && (
          <>
            <h2 className={styles.researchersTitle}>Research Team</h2>
            <div className={styles.researchersGrid}>
              {project.researchers.map((researcher, index) => (
                <div key={index} className={styles.researcherCard}>
                  <div className={styles.researcherInfo}>
                    <h3>{researcher.name}</h3>
                    <span className={styles.researcherRole}>
                      {researcher.role.charAt(0).toUpperCase() +
                        researcher.role.slice(1)}
                    </span>
                    <p className={styles.researcherAffiliation}>
                      {researcher.affiliation}
                    </p>
                  </div>
                </div>
              ))}
            </div>
          </>
        )}

        {project.content && project.content.sections && 
          project.content.sections.map((section, index) => (
            <div key={index} className={styles.section}>
              <h2 className={styles.sectionTitle}>{section.title}</h2>

              {section.content && (
                <div className={styles.sectionContent}>
                  <p>{section.content}</p>
                </div>
              )}

              {section.items && (
                <div className={styles.sectionItems}>
                  {section.items.map((item, i) => (
                    <div key={i} className={styles.sectionItem}>
                      {typeof item === "string" ? (
                        <p className={styles.itemDescription}>{item}</p>
                      ) : (
                        <>
                          {item.step && (
                            <div className={styles.stepNumber}>
                              Step {item.step}
                            </div>
                          )}
                          {item.title && (
                            <h3 className={styles.itemTitle}>{item.title}</h3>
                          )}
                          <p className={styles.itemDescription}>
                            {item.description || item}
                          </p>
                        </>
                      )}
                    </div>
                  ))}
                </div>
              )}
            </div>
          ))}
      </div>

      <Footer />
    </div>
  );
};

export default ProjectItem;