import React from "react";
import styles from "../styles/Collaboration.module.css"; // ✅ Ensure correct path

const collaborators = [
  { logo: "siat.jpg" },
  { logo: "malaya.jpg" },
  { logo: "abudhabi.jpg" },
  { logo: "aerofarms.png" },
  { logo: "education.png" },
  { logo: "uaeu.jpg" },
];

const Collaboration = () => {
  return (
    <section className={styles.collaborationSection}>
      <div className={styles.collaborationGrid}>
        {collaborators.map((partner, index) => (
          <div key={index} className={styles.partnerCard}>
            <img src={`/logo/${partner.logo}`} alt="Collaborator Logo" />
          </div>
        ))}
      </div>
    </section>
  );
};

export default Collaboration;
