import React, { useState, useEffect, useRef } from "react";
import { Link } from "react-router-dom";
import { FaBars, FaTimes } from "react-icons/fa"; // ✅ Keeping "X" button for closing
import "./NavbarMobile.css"; // ✅ Ensure correct CSS import

const NavbarMobile = () => {
  const [menuOpen, setMenuOpen] = useState(false);
  const menuRef = useRef(null);

  // ✅ Close menu if clicking outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (menuRef.current && !menuRef.current.contains(event.target)) {
        setMenuOpen(false);
      }
    };

    if (menuOpen) {
      document.addEventListener("mousedown", handleClickOutside);
    } else {
      document.removeEventListener("mousedown", handleClickOutside);
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [menuOpen]);

  return (
    <>
      {/* ✅ Modern Hamburger Button */}
      {!menuOpen && (
        <button className="hamburger" onClick={() => setMenuOpen(true)}>
          <FaBars size={26} />
        </button>
      )}

      {/* ✅ Mobile Menu */}
      <div className={`mobile-menu ${menuOpen ? "open" : ""}`} ref={menuRef}>
        {/* ✅ Modern "X" Close Button */}
        <button className="close-btn" onClick={() => setMenuOpen(false)}>
          <FaTimes size={28} />
        </button>

        {/* ✅ Navigation Links */}
        <ul className="mobile-nav-links">
          <li><Link to="/" onClick={() => setMenuOpen(false)}>Home</Link></li>
          <li><Link to="/about" onClick={() => setMenuOpen(false)}>About Us</Link></li>
          <li><Link to="/events" onClick={() => setMenuOpen(false)}>Events</Link></li>
          <li><Link to="/projects" onClick={() => setMenuOpen(false)}>Projects</Link></li>
          <li><Link to="/publications" onClick={() => setMenuOpen(false)}>Publications</Link></li>
          <li><Link to="/contact" onClick={() => setMenuOpen(false)}>Contact Us</Link></li>
        </ul>
      </div>
    </>
  );
};

export default NavbarMobile;
