import React, { useState, useEffect, useRef } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import Header from "../components/Header/Header";
import Footer from "../components/Footer/Footer";
import styles from "../styles/AboutUs.module.css";


const AboutUsPage = () => {
  const [teamMembers, setTeamMembers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const navigate = useNavigate();
  const location = useLocation();
  const missionRef = useRef(null);
  const visionRef = useRef(null);
  const goalsRef = useRef(null);

  useEffect(() => {
    const animateItems = document.querySelectorAll(`.${styles["timeline-item"]}, .${styles["goal-item"]}`);
  
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry, index) => {
          if (entry.isIntersecting) {
            setTimeout(() => {
              entry.target.classList.add(styles.visible);
              entry.target.classList.remove(styles.hidden);
            }, index * 200); // staggered appearance
          }
        });
      },
      { threshold: 0.3 }
    );
  
    animateItems.forEach((item) => observer.observe(item));
  
    return () => {
      animateItems.forEach((item) => observer.unobserve(item));
    };
  }, []);
  


  useEffect(() => {
    const fetchTeamMembers = async () => {
      try {
        setLoading(true); // Make sure loading is set to true before fetching

        const response = await fetch("http://localhost:4000/api/members");
        if (!response.ok) {
          throw new Error("Failed to fetch members");
        }
        const data = await response.json();

        // Filter for directors and investigators, then combine them
        const directors = data.filter(
          (member) => member.member_type === "Director"
        );
        const investigators = data.filter(
          (member) => member.member_type === "Investigator"
        );

        // Combine and limit to 4 members total
        const featuredMembers = [...directors, ...investigators].slice(0, 4);
        setTeamMembers(featuredMembers);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchTeamMembers();
  }, []);

  useEffect(() => {
    const scrollToSection = () => {
      let sectionId;
      
      // Check for section in state (from navigation)
      if (location.state?.scrollTo) {
        sectionId = location.state.scrollTo;
      }
      // Check for hash in URL
      else if (location.hash) {
        sectionId = location.hash.replace('#', '');
      }

      if (sectionId) {
        // Wait for the page to fully render
        setTimeout(() => {
          const element = document.getElementById(sectionId);
          if (element) {
            element.scrollIntoView({ behavior: 'smooth' });
          }
        }, 100);
      }
    };

    scrollToSection();
  }, [location]);


  const handleShowAllMembers = () => {
    navigate("/members");
  };

  if (loading) {
    return (
      <div className={styles["main-content"]}>
        <Header title="About Us" subtitle="Data Science and Analytics Lab" />
        
        {/* Show a loading screen in place of the content */}
        <div style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          height: "calc(100vh - 80px)", // Adjust height to fit below header
          width: "100vw",
          background: "black",
          color: "white",
          fontSize: "24px",
          fontWeight: "bold",
          textTransform: "uppercase",
        }}>
          Loading...
        </div>
  
        <Footer />
      </div>
    );
  }
  
    if (error) return <div>Error: {error}</div>;
  

  return (
    <div className={styles["main-content"]}>
      <Header title={"About Us"} subtitle={"Data Science and Analytics Lab"} />

      {/* Mission Section */}
      <div className={styles.section}>
        <div id="mission" className={styles["section-content"]}>
          <h2>Our Mission</h2>
          <p>
            To ingrain a sustained impact through groundbreaking data science
            research and services.
          </p>
        </div>
        <img src="mision.jpg" alt="Mission Image" />
      </div>

      {/* Vision Section */}
      <div className={`${styles.section} ${styles["vision-section"]}`}>
        <div className={styles["image-container"]}>
          <img src="vision.jpg" alt="Vision Image" />
        </div>
        <div id="vision" className={styles["text-container"]}>
          <h2>Our Vision</h2>
          <p>From Data to Impact.</p>
        </div>
      </div>
  
  
      {/* Goals Section */}
      <div id="goals" className={styles["goals-timeline"]} ref={goalsRef}>
      <h2 className={styles["goals-title"]}>Our Goals</h2>
      <p className={styles["section-description"]}>
          At DSAL, we aim to drive meaningful change through data science
          innovation. Our goals focus on research, collaboration, and skill
          development.
        </p>
        <div className={styles.timeline}>
          <div className={`${styles["timeline-item"]} ${styles.hidden}`}>
            <div className={styles["timeline-icon"]}>
              <i className="fas fa-flask"></i>
            </div>
            <div className={styles["timeline-content"]}>
              <h3>Innovative Research</h3>
              <p>
                Our team endeavors to produce profound research and actionable
                findings.
              </p>
            </div>
          </div>
          <div className={`${styles["timeline-item"]} ${styles.hidden}`}>
            <div className={styles["timeline-icon"]}>
              <i className="fas fa-handshake"></i>
            </div>
            <div className={styles["timeline-content"]}>
              <h3>Collaboration</h3>
              <p>
                Working closely with diverse faculties and industries for
                holistic data solutions.
              </p>
            </div>
          </div>
          <div className={`${styles["timeline-item"]} ${styles.hidden}`}>
            <div className={styles["timeline-icon"]}>
              <i className="fas fa-graduation-cap"></i>
            </div>
            <div className={styles["timeline-content"]}>
              <h3>Student Development</h3>
              <p>
                Providing students with immersive experiences and projects to
                hone their skills.
              </p>
            </div>
          </div>
          <div className={`${styles["timeline-item"]} ${styles.hidden}`}>
            <div className={styles["timeline-icon"]}>
              <i className="fas fa-chart-line"></i>
            </div>
            <div className={styles["timeline-content"]}>
              <h3>Data Advancements</h3>
              <p>
                Ensuring we are always aligned with the latest trends and
                advancements in data science.
              </p>
            </div>
          </div>
        </div>
      </div>
      {/* white section  */}
      <div className={styles["white-section"]}></div>
      <div className={styles["white-section"]}></div>
      {/* Team Section */}
      <div className={styles["content-section"]}>
        <div className={styles["team-header"]}>
          <div className={styles["team-title"]}>
            <h4>Our Team</h4>
            <h2>Our Members</h2>
          </div>
          <div className={styles["view-more"]}>
            <button onClick={handleShowAllMembers}>See All Members</button>
          </div>
        </div>

        <div className={styles["team-section"]}>
  {teamMembers.map((member) => (
    <div
      key={member.member_id}
      className={styles["team-member"]}
      onClick={() => navigate(`/member/${member.member_id}`)}
      style={{ cursor: 'pointer' }}
    >
      <img
        src={member.profile_image || "person.jpg"}
        alt={`${member.first_name} ${member.last_name}`}
      />
      <h3>{member.title}</h3>
      <p>{`${member.first_name} ${member.last_name}`}</p>
    </div>
  ))}
</div>
      </div>
      <Footer />
    </div>
  );
};

export default AboutUsPage;
