
import React, { useState, useEffect, useRef } from "react";
import { useParams, Link, useLocation } from "react-router-dom";
import Header from "../components/Header/Header";
import styles from "../styles/PublicationsDetails.module.css";
import {
  Facebook,
  Twitter,
  Mail,
  Link as LinkIcon,
  ExternalLink,
} from "lucide-react";

const PublicationItem = () => {
  const { doi } = useParams();
  const [publication, setPublication] = useState(null);
  const [loading, setLoading] = useState(true);
  const contentSectionRef = useRef(null);
  const location = useLocation();

  useEffect(() => {
    const fetchPublication = async () => {
      try {
        const decodedDoi = decodeURIComponent(doi);
        const response = await fetch(
          `http://localhost:4000/api/publications/${encodeURIComponent(decodedDoi)}`
        );

        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();
        setPublication(data);
        
        // Scroll to content section after data is loaded
        if (contentSectionRef.current) {
          contentSectionRef.current.scrollIntoView({ behavior: 'smooth' });
        }
      } catch (error) {
        console.error("Error fetching publication:", error);
      } finally {
        setLoading(false);
      }
    };

    if (doi) {
      fetchPublication();
    }
  }, [doi]);

  const handleShare = (platform) => {
    const url = window.location.href;
    const title = publication?.title;

    switch (platform) {
      case "facebook":
        window.open(
          `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(
            url
          )}`,
          "_blank"
        );
        break;
      case "twitter":
        window.open(
          `https://twitter.com/intent/tweet?url=${encodeURIComponent(
            url
          )}&text=${encodeURIComponent(title)}`,
          "_blank"
        );
        break;
      case "email":
        window.open(
          `mailto:?subject=${encodeURIComponent(
            title
          )}&body=${encodeURIComponent(url)}`,
          "_blank"
        );
        break;
      case "copyLink":
        navigator.clipboard.writeText(url);
        break;
      default:
        break;
    }
  };

  if (loading) {
    return <div className={styles.loading}>Loading...</div>;
  }

  if (!publication) {
    return <div className={styles.error}>Publication not found</div>;
  }
  
//  show 10 words from the title not all 
  const truncateTitle = (title, wordLimit = 10) => {
    if (!title) return '';
    const words = title.split(' ');
    if (words.length <= wordLimit) return title;
    return words.slice(0, wordLimit).join(' ') + '...';
  };
  
  return (
    <div className={styles["publication-detail-page"]}>
      <div className={styles["top-section"]}>
        <Header
          title={"Publications"}
          subtitle={
            loading
              ? "Loading..."
              : truncateTitle(publication?.title) || "Publication Not Found"
          }
        />
      </div>
   
  

      <main className={styles["main-content"]}>
        <section className={styles["header-section"]}>
          <div className={styles.container}>
            <div className={styles["publication-header"]}>
              <div className={styles.breadcrumb}>
                <Link to="/">Home</Link>
                <span> / </span>
                <Link to="/publications">Publications</Link>
                <span> / </span>
                <span>Publication</span>
              </div>

              <div className={styles["publication-meta"]}>
                <div className={styles.keywords}>
                  {publication.keywords?.split(",").map((keyword, index) => (
                    <Link
                      key={index}
                      to={`/publications?keyword=${encodeURIComponent(
                        keyword.trim()
                      )}`}
                      className={styles["keyword-tag"]}
                      style={{
                        color:
                          publication.keyword_colors
                            ?.split(",")
                            [index]?.trim() || "#000",
                      }}
                    >
                      {keyword.trim()}
                    </Link>
                  ))}
                </div>

                <div className={styles.venue}>
                  <Link
                    to={`/publications?${
                      publication.type === "Journal Article"
                        ? "journal"
                        : "conference"
                    }=${encodeURIComponent(publication.journal_title)}`}
                    className={styles["venue-link"]}
                  >
                    {publication.journal_title}
                  </Link>
                </div>
              </div>

              <div className={styles["publication-type"]}ref={contentSectionRef}>
                <Link
                  to={`/publications?type=${encodeURIComponent(
                    publication.type
                  )}`}
                  className={styles["type-tag"]}
                >
                  {publication.type}
                </Link>
                <span> | </span>
                <span>{publication.publication_year}</span>
              </div>

              <h1 className={styles["publication-title"]}>
                {publication.title}
              </h1>

              <div className={styles.authors}>
                {publication.authorDetails?.map((author, index) => (
                  <React.Fragment key={author.author_id}>
                    {author.is_member ? (
                      <Link to={`/members/${author.member_id}`}>
                        {author.firstname} {author.lastname}
                      </Link>
                    ) : (
                      <span>{author.firstname} {author.lastname}</span>
                    )}
                    {index < publication.authorDetails.length - 1 && " | "}
                  </React.Fragment>
                ))}
              </div>
            </div>
          </div>
        </section>

        <section className={styles["content-section"]} >
          <div className={styles.container}>
            <div className={styles["content-layout"]}>
              <div className={styles.sidebar}>
                <div className={styles["action-buttons"]}>
                  <a
                    href={`https://doi.org/${publication.doi}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className={styles["view-publication"]}
                  >
                    View publication <ExternalLink size={16} />
                  </a>

                  <button className={styles["export-citation"]}>
                    Export Citation
                  </button>

                  <div className={styles["share-buttons"]}>
                    <button
                      onClick={() => handleShare("facebook")}
                      aria-label="Share on Facebook"
                    >
                      <Facebook size={20} />
                    </button>
                    <button
                      onClick={() => handleShare("twitter")}
                      aria-label="Share on Twitter"
                    >
                      <Twitter size={20} />
                    </button>
                    <button
                      onClick={() => handleShare("email")}
                      aria-label="Share via Email"
                    >
                      <Mail size={20} />
                    </button>
                    <button
                      onClick={() => handleShare("copyLink")}
                      aria-label="Copy Link"
                    >
                      <LinkIcon size={20} />
                    </button>
                  </div>
                </div>
              </div>

              <div className={styles["main-content"]}>
                <div className={styles.abstract}>
                  <p>{publication.abstract}</p>
                </div>
              </div>
            </div>
          </div>
        </section>
      </main>
    </div>
  );
};

export default PublicationItem;
